    import {
        mapState,
        mapMutations
    } from 'vuex'

    export default {
        computed: {
            ...mapState(['hasLogin', 'forcedLogin'])
        },
        methods: {
            ...mapMutations(['logout']),
            bindLogin() {
                uni.navigateTo({
                    url: '../login/login',
                });
            },
            bindLogout() {
				uni.showModal({
					title:'提示',
					content:'是否注销',
					success: ((res) => {
						if (res.confirm) {
							this.logout();
							if (this.forcedLogin) {
								uni.reLaunch({
									url: '../login/login',
								});
							}
						}
					})
				})
            }
        }
    }
